
.pull-box {
  height: calc(100vh - 45px);
  border: 0px solid green;
  overflow-y: scroll;
}

.plain {
  color: #f64000;
  border: 1px solid #ff2a00;
}

.color {
  background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
  color: #ffffff;
}

.color1 {
  background: linear-gradient(270deg, #ff5736 0%, #ff2a00 100%);
  color: #ffffff;
}

.color0 {
  display: none;
  border: 0;
}

.title {
  font-size: 17px;
  font-weight: normal;
  color: #000000;
  line-height: 17px;
}

.main {
  font-family: PingFang SC-中等, PingFang SC;
}

::v-deep .van-nav-bar__content .van-icon {
  color: black;
}

::v-deep .van-popup .van-icon {
  color: black;
  font-weight: bold;
}

.fsize14 {
  color: #333333;
  font-size: 14px;
  line-height: 16px;
}

.fsize12 {
  font-size: 12px;
  color: #666666;
  line-height: 12px;
}

.fsize13 {
  font-size: 13px;
  line-height: 13px;
  height: 30px;
  border-radius: 1.4rem;
}

.pop-title {
  padding-top: 40px;
  font-size: 20px;
  color: #f92c1a;
  line-height: 20px;
}

.pop-content {
  padding-top: 30px;
  padding-bottom: 14px;
  color: #333333;
  line-height: 10px;
}

.label-man {
  position: absolute;
  left: 30px;
  bottom: 7px;
  border: 0px solid red;
  width: 13px;
  height: 14px;
  overflow: hidden;
}

.menu-box {
  background: #f8f8f8;
  border-radius: 8px;

  font-size: 14px;
}

.fuda-label {
  display: inline-block;
  font-size: 13px;
  line-height: 13px;
  margin-left: 14px;
}

.bottom-line {
  border-bottom: 1px solid #ff2a00;
}

.blue {
  color: #3f86ff;
}

.green {
  color: #5dd34a;
}

.red {
  color: #ff2a00;
}

.ofcolor1 {
  color: #ffb200;
}

.ofcolor2 {
  color: #a0acbc;
}

.ofcolor3 {
  color: #c7a88b;
}

.ofcolor0 {
  color: #333333;
}

.listfront {
  display: inline-block;
  padding-right: 20px;
  padding-bottom: 10px;
  font-size: 14px;
  width: 3.3rem;
}

.overtext {
  display: inline-block;
  max-width: 200px;
  /* border: 1px solid green; */
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
